@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: linear-gradient(to right top, #5e628f, #a16399);
  font-family: "Poppins", sans-serif;
}

#root {
  width: 100vw;
  height: 100vh;
}

.App {
  position: relative;
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  padding: 3.5vw;
}

.calender-weather-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-end;
          align-items: flex-end;
}

.clock {
  color: white;
  margin-bottom: 0.5em;
}

.hours,
.minutes {
  font-size: 5em;
}

.period {
  font-size: 2em;
}

.react-calendar {
  width: 350px;
  max-width: 100%;
  background: rgba(255, 255, 255, 0.11);
  line-height: 1.125em;
  color: white;
  border-radius: 20px;
}
.react-calendar--doubleView {
  width: 700px;
}
.react-calendar--doubleView .react-calendar__viewContainer {
  display: -webkit-flex;
  display: flex;
  margin: -0.5em;
}
.react-calendar--doubleView .react-calendar__viewContainer > * {
  width: 50%;
  margin: 0.5em;
}
.react-calendar,
.react-calendar *,
.react-calendar *:before,
.react-calendar *:after {
  box-sizing: border-box;
}
.react-calendar button {
  margin: 0;
  border: 0;
  outline: none;
}
.react-calendar button:enabled:hover {
  cursor: pointer;
}
.react-calendar__navigation {
  background-color: #e6e6e628;
  height: 44px;
  margin-bottom: 1em;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}
.react-calendar__navigation button {
  min-width: 44px;
  background: none;
  color: white;
}
.react-calendar__navigation button:enabled:hover {
  background-color: #e6e6e636;
}

.react-calendar__month-view__weekdays {
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 0.75em;
}
.react-calendar__month-view__weekdays__weekday {
  padding: 0.5em;
}
.react-calendar__month-view__weekNumbers {
  font-weight: bold;
}
.react-calendar__month-view__weekNumbers .react-calendar__tile {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 0.75em;
  padding: calc(0.75em / 0.75) calc(0.5em / 0.75);
}

.react-calendar__year-view .react-calendar__tile,
.react-calendar__decade-view .react-calendar__tile,
.react-calendar__century-view .react-calendar__tile {
  padding: 2em 0.5em;
  color: white;
}
.react-calendar__tile {
  max-width: 100%;
  text-align: center;
  padding: 0.75em 0.5em;
  background: none;
  color: white;
}

.react-calendar__month-view__days__day--neighboringMonth {
  color: #e6e6e662;
}

.react-calendar__tile:enabled:hover,
.react-calendar__tile:enabled:focus {
  background-color: #e6e6e628;
}
.react-calendar__tile--now {
  background: #e6e6e652;
}
.react-calendar__tile--now:enabled:hover,
.react-calendar__tile--now:enabled:focus {
  background: #e6e6e64d;
}
.react-calendar__tile--hasActive {
  background: #e6e6e628;
}
.react-calendar__tile--active:enabled:hover,
.react-calendar__tile--active:enabled:focus {
  background: #e6e6e638;
}
.react-calendar--selectRange .react-calendar__tile--hover {
  background-color: #e6e6e6;
}

.weather {
  color: white;
  background-color: rgba(255, 255, 255, 0.11);
  width: 42em;
  margin-left: 2.5em;
  border-radius: 20px;
}

.weather-location {
  font-size: 1.2em;
  background-color: #e6e6e628;
  padding: 0.2em 1em;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

.weather-content {
  display: -webkit-flex;
  display: flex;
  height: 10em;
}

.current-weather {
  width: 10em;
  text-align: center;
}
.current-text {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin-top: -1em;
}
.current-temp {
  font-size: 2em;
  padding-right: 0.2em;
}

.current-description {
  font-size: 1.2em;
  padding-left: 0.2em;
}

.forecast-weather {
  list-style: none;
  display: -webkit-flex;
  display: flex;
}

.forecast-weather li {
  width: 4em;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.todolist {
  position: absolute;
  top: 3.5vw;
  right: calc(3.5vw + 6.7em);
  min-width: 23em;
  height: 50vh;
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  background-color: rgba(255, 255, 255, 0.11);
  border-radius: 20px;
}

input {
  background-color: #e6e6e63f;
  border: none;
  outline: none;
  color: white;
  padding: 0 0.3em;
  margin-right: 0.2em;
  font-size: 1.1em;
  width: 10em;
  line-height: 1.5em;
}

button {
  color: white;
  background-color: transparent;
  border: none;
  cursor: pointer;
  margin: 0.23em;
}

.todos {
  overflow: auto;
  height: 30vh;
}

.todos li {
  margin-bottom: 0.6em;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.todos li span {
  width: 14em;
  background-color: #e6e6e63f;
  margin-right: 0.2em;
  padding: 0 0.3em;
}

.completed {
  text-decoration: line-through;
  text-decoration-thickness: 0.15em;
}

.links {
  position: absolute;
  top: 3.5vw;
  right: 3.5vw;
  color: white;
  font-size: 2.3em;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  height: calc(100vh - 7vw);
  background-color: rgba(255, 255, 255, 0.11);
  border-radius: 20px;
  padding: 0 0.4em;
}

a:link,
a:visited,
a:active {
  color: inherit;
}

.link:active {
  -webkit-transform: scale(0.85);
          transform: scale(0.85);
}

